import React from 'react';
import { ContactBottom, ContactInfo, ContactLayout, ContactTop , FormContact} from '../styles/contactStyle';
import { Button, Col, Form, Input, InputNumber, Row } from 'antd';
import Btn from '../components/Btn';
import { PhoneOutlined ,MailOutlined,EnvironmentOutlined} from '@ant-design/icons';

const Contact = () => {

  const layout = {
    labelCol: {
      span: 8,
    },
    wrapperCol: {
      span: 16,
    },
  };

  const validateMessages = {
    required: '${label} is required!',
    types: {
      email: '${label} is not a valid email!',
      number: '${label} is not a valid number!',
    },
    number: {
      range: '${label} must be between ${min} and ${max}',
    },
  };

  const onFinish = (values) => {
    console.log(values);
  };
    return (
        <ContactLayout>
          <ContactTop>
            <h3>Lin h vi chng ti</h3>
            <p>Hy cho chng ti bit mi cm nhn v gp  ca bn v sn phm </p>
          </ContactTop>
          <ContactBottom>
<Row>
<Col span={12}>
<FormContact
    {...layout}
    name="nest-messages"
    onFinish={onFinish}
    style={{
      maxWidth: 600,
    }}
    validateMessages={validateMessages}
  >
    <h3
    style={{
      textAlign:"center",
      fontWeight:"bold",
      marginBottom:"30px"
    }}
    >Gi li nhn</h3>
    <Form.Item
      name={['user', 'name']}
      label="H v Tn"
      rules={[
        {
          required: true,
        },
      ]}
    >
      <Input />
    </Form.Item>
    <Form.Item
      name={['user', 'email']}
      label="Email"
      rules={[
        {
          type: 'email',
        },
      ]}
    >
      <Input />
    </Form.Item>
    <Form.Item
      name={['user', 'phone']}
      label="S in thoi"
      rules={[
        {
          type: 'number',
        },
      ]}
    >
      <InputNumber />
    </Form.Item>
    <Form.Item name={['user', 'message']} label="Li nhn">
      <Input.TextArea />
    </Form.Item>
    <Form.Item
      wrapperCol={{
        ...layout.wrapperCol,
        offset: 8,
      }}
    >
      <Btn text='Gi' type="primary" htmlType="submit">
        
      </Btn>
    </Form.Item>
  </FormContact>
</Col>
<Col span={12}>
<ContactInfo>
    <h3>Thng tin lin h</h3>
    <p>
    <PhoneOutlined />
    <span>   012345678</span>
    </p>
    <p>
    <MailOutlined />
    <span>   moonRoseFashion@gmail.com</span>
    </p>
    <p>
    <EnvironmentOutlined />
    <span>   Tng 8 - S 2 Tn Tht Tng - ng a - H Ni</span>
    </p>
  </ContactInfo>
  </Col>
</Row>
          </ContactBottom>
        </ContactLayout>
    );
};

export default Contact;